  margin-right: auto;
  padding-bottom: 32px;
  padding-left: 16px;
  padding-right: 16px;
}

.contacts-list {
  background-color: var(--secondary-text-color);
  border-radius: 24px;
  margin-left: auto;
  margin-right: auto;
  padding: 40px 32px;
  box-shadow: 0 8px 30px rgba(212, 20, 67, .1);
}

.contacts-list:not(:last-child) {
  margin-bottom: 20px;
}

.contacts-list__title {
  width: 80px;
  height: 16px;
  background-color: var(--accent-color);
  text-align: center;
  text-transform: uppercase;
