}

.contacts-button {
  color: inherit;
  cursor: pointer;
  width: 196px;
  height: 42px;
  border: 2px solid var(--accent-color);
  text-align: center;
  letter-spacing: .06em;
  transition: border color stroke var(--animation);
  background-color: rgba(0, 0, 0, 0);
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  font-family: inherit;
  font-size: 14px;
  font-weight: 600;
  line-height: 1.29;
  display: flex;
}

.contacts-button:not(:last-child) {
  background-color: var(--accent-color);
  margin-bottom: 16px;
}

.contacts-button:hover, .contacts-button:focus {
  border: 2px solid #0a8817;
}

.contacts-button:focus {
  color: var(--secondary-text-color);
}

.contacts-button:disabled {
  border: 2px solid #ababab;
}

.contacts-button__icon {
  stroke: var(--primary-text-color);
  margin-left: 6px;
}

.contacts-button:focus .contacts-button__icon {
  stroke: var(--secondary-text-color);
}

.contacts-button--locations {
  transition: background-color var(--animation);
  border: none;
}

.contacts-button--locations:hover, .contacts-button--locations:focus {
  background-color: #0a8817;
  border: none;
}

.contacts-button--locations:disabled {
  background-color: #ababab;
}

